import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogTitle,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Star, CreditCard, Check, X, Zap, Shield, Clock } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import { loadStripe } from '@stripe/stripe-js';
import {
  Elements,
  CardNumberElement,
  CardExpiryElement,
  CardCvcElement,
  useStripe,
  useElements
} from '@stripe/react-stripe-js';

const stripePromise = loadStripe(import.meta.env.VITE_STRIPE_PUBLISHABLE_KEY).then(stripe => {
  console.log('✅ Stripe loaded successfully:', !!stripe);
  if (!stripe) {
    console.error('❌ Failed to load Stripe');
  }
  return stripe;
}).catch(error => {
  console.error('❌ Error loading Stripe:', error);
  return null;
});

const PaymentForm = ({ selectedPackage, onSuccess, onCancel }) => {
  const stripe = useStripe();
  const elements = useElements();
  const [processing, setProcessing] = useState(false);
  const [clientSecret, setClientSecret] = useState('');

  useEffect(() => {
    if (selectedPackage) {
      createPaymentIntent();
    }
  }, [selectedPackage]);

  const createPaymentIntent = async () => {
    try {
      const response = await axiosInstance.post('/api/credits/purchase', {
        packageId: selectedPackage.package_id
      });
      setClientSecret(response.data.data.client_secret);
    } catch (error) {
      console.error('Error creating payment intent:', error);
      toast.error('Failed to initialize payment');
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!stripe || !elements || !clientSecret) {
      return;
    }

    setProcessing(true);

    const { error, paymentIntent } = await stripe.confirmCardPayment(clientSecret, {
      payment_method: {
        card: elements.getElement(CardNumberElement),
      }
    });

    if (error) {
      console.error('Payment failed:', error);
      toast.error(error.message || 'Payment failed');
      setProcessing(false);
    } else if (paymentIntent.status === 'succeeded') {
      // Show immediate success message
      toast.success(`Payment successful! ${selectedPackage.total_credits} credits added to your account.`);

      // Wait a moment for webhook processing, then check payment status
      setTimeout(async () => {
        try {
          await axiosInstance.get(`/api/credits/payment-status/${paymentIntent.id}`);
          // Force refresh of credit balance
          window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));
        } catch (error) {
          console.warn('Payment status check failed:', error);
        }
      }, 2000);

      onSuccess(paymentIntent);
    }
  };

  return (
    <div className="space-y-6">
      {/* Order Summary */}
      <div className="bg-gradient-to-r from-blue-50 to-purple-50 rounded-2xl p-6 border border-blue-100">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Order Summary</h3>
        <div className="space-y-3">
          <div className="flex justify-between items-center">
            <span className="text-gray-700">{selectedPackage.name}</span>
            <span className="font-semibold text-gray-900">${selectedPackage.price_dollars}</span>
          </div>
          <div className="flex justify-between items-center">
            <span className="text-gray-700">Credits</span>
            <span className="font-semibold text-gray-900">{selectedPackage.total_credits.toLocaleString()}</span>
          </div>
          <div className="border-t border-blue-200 pt-3">
            <div className="flex justify-between items-center">
              <span className="text-lg font-semibold text-gray-900">Total</span>
              <span className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                ${selectedPackage.price_dollars}
              </span>
            </div>
          </div>
        </div>
      </div>

      {/* Payment Form */}
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Card Input Section */}
        <div className="space-y-4">
          <label className="block text-lg font-semibold text-gray-900">
            Payment Information
          </label>

          {/* Card Number */}
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">
              Card Number
            </label>
            <div className="relative p-4 border-2 border-gray-200 rounded-xl focus-within:border-blue-500 focus-within:ring-4 focus-within:ring-blue-100 transition-all bg-white shadow-sm">
              {/* Card Brand Icons */}
              <div className="absolute right-4 top-1/2 transform -translate-y-1/2 flex space-x-2">
                <img
                  src="https://js.stripe.com/v3/fingerprinted/img/visa-729c05c240c4bdb47b03ac81d9945bfe.svg"
                  alt="Visa"
                  className="h-6 w-auto opacity-70"
                />
                <img
                  src="https://js.stripe.com/v3/fingerprinted/img/mastercard-4d8844094130711885b5e41b28c9848f.svg"
                  alt="Mastercard"
                  className="h-6 w-auto opacity-70"
                />
              </div>
              <CardNumberElement
                options={{
                  style: {
                    base: {
                      fontSize: '16px',
                      color: '#1f2937',
                      fontFamily: '"Inter", system-ui, sans-serif',
                      fontWeight: '500',
                      '::placeholder': {
                        color: '#9CA3AF',
                      },
                      iconColor: '#6B7280',
                    },
                    invalid: {
                      color: '#EF4444',
                      iconColor: '#EF4444',
                    },
                  },
                }}
              />
            </div>
          </div>

          {/* Expiry, CVC, and Postal Code */}
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            {/* Expiry Date */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">
                MM / YY
              </label>
              <div className="p-4 border-2 border-gray-200 rounded-xl focus-within:border-blue-500 focus-within:ring-4 focus-within:ring-blue-100 transition-all bg-white shadow-sm">
                <CardExpiryElement
                  options={{
                    style: {
                      base: {
                        fontSize: '16px',
                        color: '#1f2937',
                        fontFamily: '"Inter", system-ui, sans-serif',
                        fontWeight: '500',
                        '::placeholder': {
                          color: '#9CA3AF',
                        },
                      },
                      invalid: {
                        color: '#EF4444',
                      },
                    },
                  }}
                />
              </div>
            </div>

            {/* CVC */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">
                CVC
              </label>
              <div className="p-4 border-2 border-gray-200 rounded-xl focus-within:border-blue-500 focus-within:ring-4 focus-within:ring-blue-100 transition-all bg-white shadow-sm">
                <CardCvcElement
                  options={{
                    style: {
                      base: {
                        fontSize: '16px',
                        color: '#1f2937',
                        fontFamily: '"Inter", system-ui, sans-serif',
                        fontWeight: '500',
                        '::placeholder': {
                          color: '#9CA3AF',
                        },
                      },
                      invalid: {
                        color: '#EF4444',
                      },
                    },
                  }}
                />
              </div>
            </div>

            {/* Postal Code */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">
                Postal Code
              </label>
              <div className="p-4 border-2 border-gray-200 rounded-xl focus-within:border-blue-500 focus-within:ring-4 focus-within:ring-blue-100 transition-all bg-white shadow-sm">
                <input
                  type="text"
                  placeholder="12345"
                  className="w-full bg-transparent border-none outline-none text-gray-900 placeholder-gray-400 font-medium"
                  style={{
                    fontSize: '16px',
                    fontFamily: '"Inter", system-ui, sans-serif',
                  }}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Security Notice */}
        <div className="flex items-center space-x-3 p-4 bg-green-50 rounded-xl border border-green-200">
          <Shield className="h-5 w-5 text-green-600 flex-shrink-0" />
          <p className="text-sm text-green-800">
            Your payment information is secured with 256-bit SSL encryption
          </p>
        </div>

        {/* Action Buttons */}
        <div className="flex space-x-4 pt-2">
          <Button
            type="button"
            variant="outline"
            onClick={onCancel}
            disabled={processing}
            className="flex-1 h-14 text-lg font-semibold border-2 hover:bg-primary hover:text-white"
          >
            Back
          </Button>
          <Button
            type="submit"
            disabled={!stripe || processing}
            className="flex-1 h-14 text-lg font-semibold bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white shadow-lg hover:shadow-xl transition-all duration-300"
          >
            {processing ? (
              <>
                <div className="w-6 h-6 border-2 border-white border-t-transparent rounded-full animate-spin mr-3"></div>
                Processing...
              </>
            ) : (
              <>
                <CreditCard className="h-6 w-6 mr-3" />
                Complete Purchase
              </>
            )}
          </Button>
        </div>
      </form>
    </div>
  );
};

const PurchaseCreditsModal = ({ isOpen, onClose, onSuccess }) => {
  const [packages, setPackages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedPackage, setSelectedPackage] = useState(null);
  const [showPayment, setShowPayment] = useState(false);

  useEffect(() => {
    if (isOpen) {
      fetchPackages();
    }
  }, [isOpen]);

  const fetchPackages = async () => {
    try {
      setLoading(true);
      const response = await axiosInstance.get('/api/credits/packages');
      setPackages(response.data.data);
    } catch (error) {
      console.error('Error fetching packages:', error);
      toast.error('Failed to load credit packages');
    } finally {
      setLoading(false);
    }
  };

  const handlePackageSelect = (pkg) => {
    setSelectedPackage(pkg);
    setShowPayment(true);
  };

  const handlePaymentSuccess = (paymentIntent) => {
    setShowPayment(false);
    setSelectedPackage(null);
    onSuccess?.(paymentIntent);
    onClose();
  };

  const handlePaymentCancel = () => {
    setShowPayment(false);
    setSelectedPackage(null);
  };

  const formatPrice = (cents) => {
    return (cents / 100).toFixed(2);
  };

  if (showPayment && selectedPackage) {
    return (
      <Dialog open={isOpen} onOpenChange={onClose}>
        <DialogContent className="max-w-[1600px] p-0 bg-white shadow-2xl">
          {/* Header */}
          <div className="relative px-10 py-8 bg-gradient-to-r from-slate-50 to-gray-50 border-b border-gray-200">

            <div className="flex items-center space-x-8">
              {/* Logo */}
              <div className="flex-shrink-0">
                <div className="w-20 h-20 bg-gradient-to-br from-blue-600 to-purple-700 rounded-2xl flex items-center justify-center shadow-lg">
                  <CreditCard className="h-10 w-10 text-white" />
                </div>
              </div>

              {/* Title and Description */}
              <div className="flex-1">
                <DialogTitle className="text-3xl font-bold text-gray-900 mb-3">
                  Complete Your Purchase
                </DialogTitle>
                <DialogDescription className="text-lg text-gray-600 font-medium">
                  {selectedPackage.name} • {selectedPackage.total_credits.toLocaleString()} credits • ${selectedPackage.price_dollars}
                </DialogDescription>
              </div>
            </div>
          </div>

          {/* Two Column Layout */}
          <div className="flex min-h-[500px]">
            {/* Left Column - Package Details */}
            <div className="w-1/2 p-8 bg-gray-50 border-r border-gray-200">
              <h3 className="text-xl font-bold text-gray-900 mb-6">Details About Package</h3>

              {/* Package Summary */}
              <div className="space-y-4">
                <div className="bg-white rounded-lg p-6 shadow-sm border border-gray-200">
                  <div className="flex items-center justify-between mb-4">
                    <h4 className="text-lg font-semibold text-gray-900">{selectedPackage.name}</h4>
                    <span className="text-2xl font-bold text-blue-600">${selectedPackage.price_dollars}</span>
                  </div>

                  <div className="space-y-3">
                    <div className="flex justify-between items-center py-2 border-b border-gray-100">
                      <span className="text-gray-600">Credits</span>
                      <span className="font-semibold text-gray-900">{selectedPackage.total_credits.toLocaleString()}</span>
                    </div>

                    <div className="flex justify-between items-center py-2 border-b border-gray-100">
                      <span className="text-gray-600">Price per Credit</span>
                      <span className="font-semibold text-gray-900">${(selectedPackage.price_dollars / selectedPackage.total_credits).toFixed(4)}</span>
                    </div>

                    <div className="flex justify-between items-center py-2 border-b border-gray-100">
                      <span className="text-gray-600">Expires</span>
                      <span className="font-semibold text-gray-900">30 days from purchase</span>
                    </div>

                    <div className="flex justify-between items-center py-2 font-bold text-lg">
                      <span className="text-gray-900">Total</span>
                      <span className="text-blue-600">${selectedPackage.price_dollars}</span>
                    </div>
                  </div>
                </div>

                {/* Features */}
                <div className="bg-white rounded-lg p-6 shadow-sm border border-gray-200">
                  <h5 className="font-semibold text-gray-900 mb-3">What's Included</h5>
                  <ul className="space-y-2 text-sm text-gray-600">
                    <li className="flex items-center">
                      <div className="w-2 h-2 bg-green-500 rounded-full mr-3"></div>
                      AI-powered voice calls
                    </li>
                    <li className="flex items-center">
                      <div className="w-2 h-2 bg-green-500 rounded-full mr-3"></div>
                      Advanced analytics
                    </li>
                    <li className="flex items-center">
                      <div className="w-2 h-2 bg-green-500 rounded-full mr-3"></div>
                      24/7 support
                    </li>
                    <li className="flex items-center">
                      <div className="w-2 h-2 bg-green-500 rounded-full mr-3"></div>
                      No setup fees
                    </li>
                  </ul>
                </div>
              </div>
            </div>

            {/* Right Column - Card Details */}
            <div className="w-1/2 p-8 bg-white">
              <h3 className="text-xl font-bold text-gray-900 mb-6">Card Details</h3>

              <Elements stripe={stripePromise}>
                <PaymentForm
                  selectedPackage={selectedPackage}
                  onSuccess={handlePaymentSuccess}
                  onCancel={handlePaymentCancel}
                />
              </Elements>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    );
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-[1800px] max-h-[85vh] overflow-hidden p-0 bg-gradient-to-br from-slate-50 to-white">
        {/* Header */}
        <div className="relative px-6 pt-6 pb-4 bg-white border-b border-gray-100">
          

          <div className="flex items-center space-x-6">
            {/* Logo */}
            <div className="flex-shrink-0">
              <div className="w-16 h-16 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                <Zap className="h-8 w-8 text-white" />
              </div>
            </div>

            {/* Title and Description */}
            <div className="flex-1">
              <DialogTitle className="text-2xl font-bold bg-gradient-to-r from-gray-900 to-gray-700 bg-clip-text text-transparent mb-2">
                Choose Your Plan
              </DialogTitle>
              <DialogDescription className="text-base text-gray-600">
                Unlock the full potential of our AI platform with flexible credit packages designed for every need
              </DialogDescription>
            </div>
          </div>
        </div>

        {/* Content */}
        <div className="px-6 py-6 overflow-y-auto max-h-[calc(85vh-160px)]">
          {loading ? (
            <div className="flex flex-col items-center justify-center py-16 space-y-4">
              <div className="relative">
                <div className="w-16 h-16 border-4 border-blue-200 rounded-full animate-spin border-t-blue-600"></div>
              </div>
              <p className="text-gray-600 font-medium">Loading packages...</p>
            </div>
          ) : (
            <>
              {/* Packages Grid */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mb-6">
                {packages.map((pkg) => (
                  <div
                    key={pkg.package_id}
                    className={`relative group cursor-pointer pricing-card-hover ${
                      pkg.is_popular
                        ? 'transform scale-105 z-10'
                        : ''
                    }`}
                    onClick={() => handlePackageSelect(pkg)}
                  >
                     
                    {/* Popular Badge */}
                    {!!pkg.is_popular && (
                      <div className="absolute -top-3 left-1/2 transform -translate-x-1/2 z-20">
                        <div className="bg-gradient-to-r from-orange-400 to-pink-500 text-white px-3 py-1.5 rounded-full text-xs font-semibold shadow-lg">
                         <Star className="h-3 w-3 inline mr-1" />
                          Most Popular
                        </div>
                      </div>
                    )}

                  

                    <Card className={`h-full transition-all duration-300 ${
                      pkg.is_popular
                        ? 'gradient-border-popular popular-glow bg-white'
                        : 'border-2 border-gray-200 hover:border-blue-300 hover:shadow-xl bg-white'
                    }`}>
                      <CardContent className="p-5 h-full flex flex-col">
                        {/* Package Header */}
                        <div className="text-center mb-4">
                          <h4 className="text-lg font-bold text-gray-900 mb-1">{pkg.name}</h4>
                          <p className="text-gray-600 text-xs leading-relaxed">{pkg.description}</p>
                        </div>

                        {/* Price */}
                        <div className="text-center mb-4">
                          <div className="flex items-baseline justify-center space-x-1">
                            <span className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                              ${formatPrice(pkg.price_cents)}
                            </span>
                            <span className="text-gray-500 text-sm">USD</span>
                          </div>
                        </div>

                        {/* Credits Info */}
                        <div className="space-y-3 mb-5 flex-grow">
                          <div className="bg-gray-50 rounded-lg p-3 space-y-2">
                            <div className="flex items-center justify-between">
                              <span className="text-gray-700 text-sm font-medium">Base Credits</span>
                              <span className="text-lg font-bold text-gray-900">
                                {pkg.credits_amount.toLocaleString()}
                              </span>
                            </div>

                            {pkg.bonus_credits > 0 && (
                              <div className="flex items-center justify-between border-t border-gray-200 pt-2">
                                <span className="text-green-700 text-sm font-medium flex items-center">
                                  <Check className="h-3 w-3 mr-1" />
                                  Bonus Credits
                                </span>
                                <span className="text-lg font-bold text-green-600">
                                  +{pkg.bonus_credits.toLocaleString()}
                                </span>
                              </div>
                            )}

                            <div className="flex items-center justify-between border-t border-gray-200 pt-2">
                              <span className="text-gray-900 text-sm font-semibold">Total Credits</span>
                              <span className="text-xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                                {pkg.total_credits.toLocaleString()}
                              </span>
                            </div>
                          </div>

                          <div className="text-center">
                            <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded-full">
                              ${(pkg.price_cents / 100 / pkg.total_credits).toFixed(4)} per credit
                            </span>
                          </div>
                        </div>

                        {/* CTA Button */}
                        <Button
                          className={`w-full h-10 text-sm font-semibold transition-all duration-300 ${
                            pkg.is_popular
                              ? 'bg-gradient-to-r from-orange-500 to-pink-600 hover:from-orange-600 hover:to-pink-700 text-white shadow-lg hover:shadow-xl'
                              : 'bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white hover:shadow-lg'
                          }`}
                        >
                          Get Started
                        </Button>
                      </CardContent>
                    </Card>
                  </div>
                ))}
              </div>

              {/* Trust Indicators */}
              <div className="bg-white rounded-xl border border-gray-200 p-4">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
                  <div className="flex flex-col items-center space-y-1.5">
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <Shield className="h-4 w-4 text-green-600" />
                    </div>
                    <h4 className="text-sm font-semibold text-gray-900">Secure Payment</h4>
                    <p className="text-xs text-gray-600">Protected by Stripe's enterprise-grade security</p>
                  </div>

                  <div className="flex flex-col items-center space-y-1.5">
                    <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                      <Zap className="h-4 w-4 text-blue-600" />
                    </div>
                    <h4 className="text-sm font-semibold text-gray-900">Instant Activation</h4>
                    <p className="text-xs text-gray-600">Credits added to your account immediately</p>
                  </div>

                  <div className="flex flex-col items-center space-y-1.5">
                    <div className="w-8 h-8 bg-purple-100 rounded-full flex items-center justify-center">
                      <Clock className="h-4 w-4 text-purple-600" />
                    </div>
                    <h4 className="text-sm font-semibold text-gray-900">30-Day Validity</h4>
                    <p className="text-xs text-gray-600">Credits expire one month from purchase</p>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default PurchaseCreditsModal;
